.formularioContacto{
    background-color: var(--gris);
    width:min( 60rem, 100% ) ;
    margin: 0 auto;
    border-radius: 1rem;
    padding: 2rem   ;
    
}
.formularioContacto fieldset{
    border: none;
}

.formularioContacto legend{
    text-align: center;
    text-transform: uppercase;
    font-size: 1.8rem;
    font-weight: 700;
    color: var(--primario);
    margin-bottom: 2rem;
    ;
}
    @media (min-width: 768px) {
        .datos{
            display: grid;
            grid-template-columns: 1fr 1fr;
            column-gap: 1rem;
            grid-template-rows: auto auto 20rem;
        } 
    }
    .campo:nth-child(3){
        grid-column: 1/3;
    }
    .campo:nth-child(4){
        grid-column: 1/3;
    }
    .campo{
        margin-bottom: 1rem;

    }  
    .campo label{
        color: var(--blanco);
        font-weight: bold;
        margin-bottom: .5rem;
        display: block;
    }  
    .campo textarea{
        height: 20rem;
    }

    .input_text{
        width: 100%;
        border: none;
        padding: 1.5rem;
        border-radius: .5rem;
    }
    footer{
        text-align: center;
    }
